import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AdminPanelPage} from './admin-panel.page';

const routes: Routes = [
  {
    path: '',
    component: AdminPanelPage,
    children: [
      {
        path: '',
        loadChildren: () => import('./service-types/service-types.module').then(m => m.ServiceTypesPageModule)
      },
      {
        path: 'service-types',
        loadChildren: () => import('./service-types/service-types.module').then(m => m.ServiceTypesPageModule)
      },
      {
        path: 'service-sub-types',
        loadChildren: () => import('./service-sub-types/service-sub-types.module').then(m => m.ServiceSubTypesPageModule)
      },
      {
        path: 'services',
        loadChildren: () => import('./services/services.module').then(m => m.ServicesPageModule)
      },
      {
        path: 'users',
        loadChildren: () => import('./users/users.module').then(m => m.UsersPageModule)
      },
      {
        path: 'packages',
        loadChildren: () => import('./packages/packages.module').then(m => m.PackagesPageModule)
      }
    ]
  }];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class AdminPanelPageRoutingModule {
}
